@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@font-face {
  font-family: "Futura Medium";
  font-style: normal;
  src: local("futura-medium"),
    url(/static/media/futura-medium.9a162627.ttf) format("ttf");
}

header {
  font-family: "Futura Medium", sans-serif;
  font-weight: regular;
}

h1 {
  font-family: "Futura Medium", sans-serif;
  font-weight: regular;
}

h2 {
  font-family: "Futura Medium", sans-serif;
  font-weight: regular;
}

body {
  margin: 0;
  font-family: "Roboto";
  font-weight: regular;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App{text-align:start;display:flex;flex-direction:column;padding-bottom:150px}.padded-div{margin-top:calc(15pt + 2vmin);margin-right:calc(8pt + 8vmin);margin-left:calc(8pt + 8vmin);position:relative}.flex-row{justify-content:right;display:flex;flex-direction:row;flex-wrap:wrap;height:100%;max-width:70%}.banner-wrapper{position:relative;height:100vh}.title{z-index:10;min-height:25vh;display:flex;flex-direction:column;justify-content:left;line-height:75%;font-size:48pt;color:#0c1a28;margin-bottom:5vmin;margin-top:5vmin;letter-spacing:0.1rem;padding-bottom:10px;line-height:110%;text-decoration:underline;-webkit-text-decoration-color:#ff824a;text-decoration-color:#ff824a}.theme-toggle{position:relative;float:right;width:24;z-index:99;height:24;padding:10px}.theme-toggle:hover{width:28;height:28}.h1{flex-direction:column;position:relative;z-index:1;max-height:0vh;display:flex;justify-content:center;font-size:36pt;text-decoration:underline;-webkit-text-decoration-color:#ff824a;text-decoration-color:#ff824a;padding-top:10px;padding-bottom:10px}p.limited-width{max-width:600px;padding-bottom:50px}.url{color:black;-webkit-text-decoration-color:light-grey;text-decoration-color:light-grey;-webkit-text-decoration-line:underline;text-decoration-line:underline;-webkit-text-decoration-skip-ink:none;text-decoration-skip-ink:none}.url:hover{font-family:"DIN Next Lt Pro Medium"}@keyframes slide-in-title{from{transform:translate(-100%, -105%) scaleX(0)}to{transform:translate(-2%, -105%) scaleX(1)}}@keyframes slide-in-h1{from{transform:translate(-100%, -75%) scaleX(0)}to{transform:translate(-2%, -75%) scaleX(1)}}

.image-container{height:100%;max-width:100%;min-width:100%;z-index:0;position:absolute;justify-content:right}img.coverPhoto{object-fit:cover;height:100%;width:100%}

.project-card-button{border:1px solid #0f1923;position:relative;flex-wrap:wrap;padding:10px;margin-right:10px;display:inline-block;text-align:center;transition:0.5s;background-color:#ece8e1}.project-card-button:hover{box-shadow:inset 300px 0 1px 0 #0f1923}

